<template>
  <div class="container">
    <div class="registerBox">
      <div class="unLogin">
        <div class="doll"><img src="/img/doll.png" /></div>
        <h2>註冊帳號</h2>
        <div class="formRow">
          <h3>Email *</h3>
          <input
            id="email"
            type="email"
            name=""
            value=""
            placeholder="請輸入您的email同時為帳號"
            required
            v-model="User.Email"
          />
        </div>
        <div class="formRow">
          <h3>姓名 *</h3>
          <input
            id="name"
            type="text"
            name=""
            value=""
            placeholder="請輸入您的姓名"
            required
            v-model="User.Name"
          />
        </div>
        <div class="formRow">
          <h3>密碼 *</h3>
          <input
            id="password1"
            type="password"
            name=""
            value=""
            placeholder="請輸入密碼"
            required
            v-model="User.Password"
          /><span>密碼設置需含大小寫英文與數字</span>
        </div>
        <div class="formRow">
          <h3>確認密碼 *</h3>
          <input
            id="password2"
            type="password"
            name=""
            value=""
            placeholder="再次輸入密碼"
            required
            v-model="User.Password2"
          />
        </div>
        <div class="formRow">
          <h3>手機</h3>
          <input
            id="mobile"
            type="tel"
            name=""
            value=""
            placeholder="09xxxxxxxx"
            v-model="User.Mobile"
          /><span>海外使用者請輸入+國碼手機號碼(如+8869xxxxxxxx)</span>
        </div>
        <div class="confirmPrivacy">
          <label for="checkPolicy">
            <input type="checkbox" id="checkPolicy" v-model="IsCheck">
            您已經詳閱並同意遵守故宮線上學校<router-link to="/security" target="_blank">安全政策</router-link>及<router-link to="/privacy" target="_blank">隱私權政策</router-link>。
          </label>
        </div>
        <div class="submitBtn">
          <a @click="register" href="javascript:void(0);">註冊</a>
        </div>
        <p class="otherLink"><router-link to="/login">返回登入</router-link></p>
        <div class="formRow">
          <p>
            若有教育雲端帳號OpenID，請直接透過教育雲端帳號登入教師專區或學生專區即可
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      User: {
        Email: "",
        Name: "",
        Password: "",
        Password2: "",
        Mobile: "",
      },
      IsCheck:false
    };
  },
  mounted() {
    this.setBase("register");
  },
  methods: {
    async register() {
      if (!this.validate(".registerBox")) {
        return;
      }
      if(!this.IsCheck){
        alert('請詳閱並同意遵守故宮線上學校安全政策及隱私權政策');
        return;
      }
      if (this.User.Password != this.User.Password2) {
        alert("兩次密碼不一致，請重新輸入！");
        return;
      }
      try {
        var result = await this.$api.register(this.User);
        this.$user.setUser(result);
        this.$router.replace("/");
      } catch (e) {
        console.error(e);
        if (e.Status == 4) {
          alert("此帳號已註冊過");
        }
      }
    },
  },
};
</script>